/* Full Screen Announcement */

.taggModalShow.fade.anouncementModal .taggModal {
    width: 100vw!important;
    height: 100vh;
  }
  
  .anouncementModal .taggContentHolder {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  .taggContentHolder {
    position: absolute;
    height: 100%;
    width: 100%;
    right: 0;
    top: 0;
    padding: 1.5vw;
  }
  
  .taggImageHolder {
    background: center center no-repeat #000;
    background-size: contain;
    position: absolute;
    width: 56.25%;
    top: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
  }
  .taggModalShow.disModalPopup.modal-text-image .setFont {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 130%;
  }
  
  .taggModalShow.disModalPopup .setFont {
    font-size: 25px;
    line-height: 125%;
  }
  .taggModalShow .setFont {
    word-break: break-word;
    color: #323232;
  }
  
  .anounceTxtOnly .setFont.only-text {
    font-weight: 400;
  }
  
  .taggImageHolder img {
    max-width: 100%;
    height: auto;
    width: auto;
    display: block;
  }
  .taggModalShow.modal-only-image.fade .taggModal .taggImageHolder, .taggModalShow.modal-only-video.fade .taggModal .taggImageHolder {
    width: 100%;
    position: static;
    max-height: 60vh;
  }
  .taggModalShow.modal-only-image.fade.anouncementModal .taggModal .taggImageHolder {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    max-height: 100%;
  }
  
  .taggModalShow.modal-only-image.anouncementModal .taggImageHolder img {
    visibility: hidden;
  }
  .taggModalShow.modal-only-image .taggImageHolder img {
    visibility: visible;
    max-height: inherit;
    max-width: 100%;
    width: auto;
    margin: 0 auto;
  }
  
  .modal-only-text .taggContentHolder {
    width: 100%;
  }
  
  .anouncementModal .setFont.only-text {
    width: 90%;
    text-align: center;
    margin: 0 auto;
  }
  
  .instaMainImg img {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    height: auto;
    margin: 0 auto;
    max-height: 100%;
  }
  @media only screen and (max-width: 979px){
  .taggModalShow.disModalPopup .setFont {
    font-size: 22px!important;
  }
  }
  .rhap_container.tb_post_modal_audio {
    width: 100%;
    padding: 10px 0px;
    background-color: transparent;
    box-shadow: none;
  }
  
  .tb_post_modal_audio_thumb_blur {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    filter: blur(14px);
    background-color: #000;
    position: relative;
  }
  
  .tb_post_modal_audio_thumb {
    z-index: 1;
    width: 70%;
    height: 70%;
    position: absolute;
    left: 0;
    right: 0;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    margin: 0 auto;
    bottom: 20%;
  }
  
  .tb_post_modal_audio_wrapper {
    position: absolute;
    z-index: 1;
    bottom: 5%;
    left: 0;
    width: 100%;
    right: 0;
    max-width: 80%;
    margin: 0 auto;
  }
  
  .tb_post_modal_audio_thumb_blur:after {
    content: '';
    position: absolute;
    background-color: rgba(0,0,0,.4);
    z-index: 1;
    width: 100%;
    height: 100%;
    filter: blur(14px);
    transform: scale(1.5);
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }
  
  .tb_post_modal_audio .rhap_main-controls-button {
    margin: 0 3px;
    color: #fff;
    font-size: 50px;
    width: 50px;
    height: 50px;
  }
  
  .tb_post_modal_audio .rhap_container svg {
    width: 100%;
    height: 100%;
  }
  
  .tb_post_modal_audio .rhap_progress-indicator{
    background: #fff;
  }
  
  .tb_post_modal_audio .rhap_time{
    color: #fff;
  }
  
  .tb_post_modal_audio_wrapper .rhap_controls-section {
    flex: 0;
  }
  .fb_iframe_widget{
    display: flex !important;
    align-items: center;
  }
  .wAnnouncementsOverLay .only-text,
.wAnnouncementsOverLay .setFont {
    margin: 0;
    word-break: break-word;
    font-size: 26px;
    display: block;
    position: absolute;
    text-align: center;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
    padding: 0;
    width: 96%;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 7;
    -webkit-box-orient: vertical;
    font-weight: 600;
}
  @media screen and (max-width: 480px) {
    .tb_post_modal_audio_wrapper {
        bottom: 3%;
        max-width: 100%;
    }
    .tb_post_modal_audio_thumb {
        bottom: 25%;
    }
    .rhap_container.tb_post_modal_audio{
        padding: 10px 15px;
    }
  }